export default {
	data() {
		return {
			source: 0,
			addressList: []
		}
	},
	onLoad() {
		this.loadData();
		if (!this.addressList) {
			uni.navigateTo({
				url: '/pages/address/address'
			})
		}
	},
	methods: {
		async loadData() {
			await this.$api.account.getAddress().then(data => {
				this.addressList = data
			})
		},
		//选择地址
		checkAddress(item) {
			this.$api.account.setDefualt(item.id).then(data => {
				uni.switchTab({
					url: '/pages/cart/cart'
				})
			});
		},
		addAddress() {
			uni.navigateTo({
				url: '/pages/address/address?type=add'
			})
		},
		editAddress(item) {
			let str = encodeURIComponent(JSON.stringify(item));
			uni.navigateTo({
				url: `/pages/address/address?address=${str}`
			})
		},
		deleteAddress(item) {
			this.$api.account.deleteAddress(item.id).then(data => {
				this.loadData()
			})
		}
	}
}
